// Import Material Web button components
import '@material/web/button/filled-button.js';

// Event delegation for click handling
document.addEventListener("click", (event) => {
  const target = event.target as HTMLElement;

  // Check if the clicked element is an md-filled-button with data-target
  if (target.tagName === "MD-FILLED-BUTTON" && target.hasAttribute("data-target")) {
    const page = target.getAttribute("data-target");
    if (page) {
      console.log(`Navigating to ${page}`);
      window.location.href = page;
    } else {
      console.error("No target page specified.");
    }
  }
});
